import type { EventName } from "@lit-labs/react";
import * as WebComponentModule from "@vaadin/notification/vaadin-notification.js";
import * as React from "react";
import { createComponent, WebComponentProps } from "../utils/createComponent.js";
export type NotificationEventMap = Readonly<{
    onOpenedChanged: EventName<WebComponentModule.NotificationEventMap["opened-changed"]>;
}>;
const events = { onOpenedChanged: "opened-changed" } as NotificationEventMap;
export type NotificationProps = WebComponentProps<WebComponentModule.Notification, NotificationEventMap>;
export const Notification = createComponent({
    elementClass: WebComponentModule.Notification,
    events,
    react: React,
    tagName: "vaadin-notification"
});
export { WebComponentModule };
